<template>
  <div class="editBox">
    <!-- 费用项目 -->
    <div class="local_info">
      <span>费用项目</span>
      <img @click="addRight('add')"
           class="img_add_delete"
           src="@/assets/five/add.png"
           alt />
    </div>
    <!-- 费用列表 -->
    <div>
      <table v-for="item in lifeCosts"
             :key="item.id"
             class="info_box"
             :style="{'padding':(item.dictSort < 4)?'4px 20px':'14px 20px'}">
        <tr>
          <!-- 左边 -->
          <td>
            <img @click="remove(item)"
                 class="img_add_delete"
                 src="@/assets/five/delete.png"
                 alt />
            <div class="price_unit">
              <div>
                <span class="three"
                      :class="(item.dictSort < 4)?'w000':''"
                      :style="{'padding-bottom':(item.dictSort < 4)?'0.2rem':'0'}">{{item.dictLabel}}</span>
                <span v-if="item.dictSort == '1' && item.elecMode == '1'
                        || item.dictSort == '2' && item.waterMode == '1' 
                        || item.dictSort == '3' && item.gasMode == '1'">{{ item.Price }}{{ unit[item.dictSort] }}</span>
              </div>
              <div v-if="item.dictSort=='1' && item.elecMode=='1'
                      || item.dictSort=='2' && item.waterMode=='1'
                      || item.dictSort=='3' && item.gasMode=='1'">
                <span v-if="item.Degree> 0">本期用{{ obj[item.dictSort] }}:</span>
                <span v-if="item.Degree> 0">
                  {{ (item.Degree - item.preDegree).toFixed(2) }}{{ unit1[item.dictSort] }}&emsp;({{ item.preDegree }}-{{ item.Degree }})
                </span>
              </div>
              <div v-else>
              </div>
            </div>
          </td>
          <!-- 右边  -->
          <td v-if="item.dictSort=='1' && item.elecMode=='1'
                    || item.dictSort=='2' && item.waterMode=='1'
                    || item.dictSort=='3' && item.gasMode=='1'"
              @click="setItem(item,'edit')"
              class="f14">
            <span class="c000"
                  v-if="(item.Degree-item.preDegree)*item.Price < 0">0</span>
            <span class="c000"
                  v-else>{{ ((item.Degree-item.preDegree)*item.Price).toFixed(2) }}<span style="color:#888;padding:0 2px;">元</span></span>
            <van-icon name="arrow"
                      class="cb2" />
          </td>
          <!-- 右边  -->
          <td v-else
              class="f14"
              @click="setItem(item,'edit')">
            <span>
              <span class="c000">{{ item.elecMode || item.waterMode || item.gasMode== '2'?(item.Price).toFixed(2):item.Price }}</span>
              <span v-if="item.dictSort == '1'">{{ item.elecMode == '3'?unit1[item.dictSort]:'元' }}</span>
              <span v-else-if="item.dictSort == '2'">{{ item.waterMode == '3'?unit1[item.dictSort]:'元' }}</span>
              <span v-else-if="item.dictSort == '3'">{{ item.gasMode == '3'?unit1[item.dictSort]:'元' }}</span>
              <span v-else>元</span>
              <van-icon v-if=" item.dictSort < '4'"
                        name="arrow"
                        class="cb2" />
            </span>
          </td>
        </tr>
      </table>
    </div>
    <van-button type="primary"
                class="saveStyle"
                @click="saveBack">保存</van-button>
    <van-popup v-model="addShow"
               class="addBox">
      <div>
        <!-- 费用项目 -->
        <p class="add-title"
           v-if="objItem">{{ obj[objItem.dictSort] }}费详情</p>
        <p class="add-title"
           v-else>添加费用</p>
        <div class="add-obj"
             v-if="!objItem">
          <div>费用项目</div>
          <div @click="addObjShow = true">
            <span>{{ title }}</span>
            <van-icon name="arrow"
                      class="cb2" />
          </div>
        </div>
        <!-- 收费方式 -->
        <div class="add-obj">
          <div>收费方式</div>
          <div v-if="dictSort < 4"
               @click="add_or_edeit == 'add' ?show_lifeCostPay = true:''">
            <span>{{ lifeCost_mode }}</span>
            <van-icon v-if="add_or_edeit == 'add'"
                      name="arrow"
                      class="cb2" />
          </div>
          <div v-else>固定收取</div>
        </div>
        <!-- 单价 -->
        <div class="add-obj">
          <span class="w30">单价</span>
          <input @keyup="clearNoNum(addPrice,name = 'price')"
                 type="text"
                 v-model="addPrice"
                 placeholder="请输入单价"
                 class="addInput60">
          <div v-if="objItem && objItem.dictSort == '2'"
               class="w10">元/月</div>
          <div v-else-if="objItem && objItem.dictSort != '2'"
               class="w10">{{ lifeCost_mode == '抄表计算' ? unit[objItem.dictSort] : unit1[objItem.dictSort] }}</div>
          <div v-if="!objItem && lifeCost_mode == '固定收取'"
               class="w10">元/月</div>
          <div v-else-if="!objItem && lifeCost_mode != '固定收取'"
               class="w10">{{ lifeCost_mode == '抄表计算' ? unit[dictSort] : unit1[dictSort] }}</div>
        </div>
        <!-- 上次读数 -->
        <div class="add-obj"
             v-if="dictSort < 4 && lifeCost_mode == '抄表计算'">
          <span class="w30 ">上次读数</span>
          <input v-if="objItem"
                 readonly
                 type="text"
                 v-model="preDegree"
                 placeholder="请输入上次读数"
                 class="addInput60 cb2">
          <input v-else
                 @keyup="clearNoNum(preDegree,name = 'lastNum')"
                 type="text"
                 v-model="preDegree"
                 placeholder="请输入上次读数"
                 class="addInput60">
          <div class="w10"></div>
        </div>
        <!-- 本次读数 -->
        <div class="add-obj"
             v-if="dictSort < 4 && lifeCost_mode == '抄表计算'">
          <span class="w30">本次读数</span>
          <input @keyup="clearNoNum(Degree,name = 'nowNum')"
                 type="text"
                 v-model="Degree"
                 placeholder="请输入本次读数"
                 class="addInput60">
          <div class="w10"></div>
        </div>
        <!-- 应收费用 -->
        <div class="add-obj"
             v-if="objItem && lifeCost_mode == '抄表计算'">
          <span class="w30">应收费用</span>
          <span class="addInput60 c60">{{ (Degree - preDegree) * addPrice | filterPrice}}&ensp;<span class="w10">元</span></span>
          <div class="w10"></div>
        </div>
        <div class="add-button">
          <div class="add-cancle"
               @click="addShow = false">取消</div>
          <div class="add-sure"
               @click="addSure">确定</div>
        </div>
      </div>
    </van-popup>
    <!-- 选择-费用项目 -->
    <van-popup v-model="addObjShow"
               round
               position="bottom">
      <van-picker show-toolbar
                  :columns="columns"
                  value-key='dictLabel'
                  @cancel="addObjShow = false"
                  @confirm="onConfirm" />
    </van-popup>
    <!-- 选择-收费方式 -->
    <van-popup v-model="show_lifeCostPay"
               position="bottom">
      <van-picker show-toolbar
                  :columns="payAll"
                  @confirm="onConfirmCostPayType"
                  @cancel="show_lifeCostPay = false" />
    </van-popup>
  </div>
</template>
<script>
import { Toast } from 'vant';
import { MessageBox } from 'mint-ui';
import { GetSystemDictDataByType } from '@/api/api_query';
import { GetBusinessLifeCostBill, PostBusinessLifeCostBillCheckTypeAdd, PostBusinessLifeCostBillCheckTypeRemove, PostBusinessLifeCostBillEdit } from '@/api/api_business_hotel_and_scale';
import { GetBusinessCooperLifeCostBill, PostBusinessCooperLifeCostBillEdit, PostBusinessCooperLifeCostBillCheckTypeAdd, PostBusinessCooperLifeCostBillCheckTypeRemove } from '@/api/api_business.js';
export default {
    data() {
        return {
            lifeCosts: [],
            editPrice: {},
            obj: { 1: '电', 2: '水', 3: '气' },
            unit: { 1: '元/度', 2: '元/吨', 3: '元/m³' },
            unit1: { 1: '度', 2: '吨', 3: 'm³' },
            addShow: false,
            addPrice: '',
            addObjShow: false,
            show_lifeCostPay: false,
            columns: [],
            payAll: [],
            title: '',
            dictSort: '',
            objItem: '',
            detailPrice: '',
            preDegree: null,
            Degree: null,
            lifeCost_mode: null,
            add_or_edeit: null
        };
    },
    created() {
        this.$store.commit('setHeader', '编辑账单');
        if (this.$route.query.cooperLifeCostBillId) {
            this.getCurrentCooperInfo(this.$route.query.cooperLifeCostBillId);
        } else {
            this.getCurrentInfo(this.$route.query.lifeCostBillId);
        }
    },
    filters: {
        filterPrice(price) {
            return Number(price).toFixed(2);
        }
    },
    methods: {
        // 当前信息
        getCurrentInfo(id) {
            GetBusinessLifeCostBill(id).done((res) => {
                this.currentInfo = res.data;
                this.getMore();
            });
        },
        // 当前信息-cooper
        getCurrentCooperInfo(id) {
            GetBusinessCooperLifeCostBill({ cooperLifeCostBillId: id }).done((res) => {
                this.currentInfo = res.data;
                this.getMore();
            });
        },
        getMore() {
            GetSystemDictDataByType({ dictType: 'tb_business_life_cost_mode' }).done((res) => {
                this.payOldAll = res.data;
                this.payAll = res.data.map((item) => item.dictLabel);
                this.lifeCost_mode = this.payOldAll[0].dictLabel;
            });
            GetSystemDictDataByType({ dictType: 'business_life_cost' }).done((res) => {
                let lifeCosts = res.data.map((item) => {
                    switch (item.dictValue) {
                        case 'elecPrice': {
                            item.Price = this.currentInfo.elecPrice || 0;
                            item.Degree = this.currentInfo.elecDegree || this.currentInfo.preElecDegree || 0;
                            item.preDegree = this.currentInfo.preElecDegree || 0;
                            item.elecMode = this.currentInfo.elecMode;
                            break;
                        }
                        case 'waterPrice': {
                            item.Price = this.currentInfo.waterPrice || 0;
                            item.Degree = this.currentInfo.waterDegree || this.currentInfo.preWaterDegree || 0;
                            item.preDegree = this.currentInfo.preWaterDegree || 0;
                            item.waterMode = this.currentInfo.waterMode;
                            break;
                        }
                        case 'gasPrice': {
                            item.Price = this.currentInfo.gasPrice || 0;
                            item.Degree = this.currentInfo.gasDegree || this.currentInfo.preGasDegree || 0;
                            item.preDegree = this.currentInfo.preGasDegree || 0;
                            item.gasMode = this.currentInfo.gasMode;
                            break;
                        }
                        case 'propPrice': {
                            item.Price = this.currentInfo.propPrice;
                            break;
                        }
                        case 'rubbishPrice': {
                            item.Price = this.currentInfo.rubbishPrice;
                            break;
                        }
                        case 'parkPrice': {
                            item.Price = this.currentInfo.parkPrice;
                            break;
                        }
                        case 'broadBandPrice': {
                            item.Price = this.currentInfo.broadBandPrice;
                            break;
                        }
                        default: {
                            item.showlength = -1;
                            break;
                        }
                    }
                    return item;
                });
                this.oldLifeCosts = lifeCosts;
                this.lifeCosts = lifeCosts.filter((item) => item.Price > 0);
            });
        },
        // 匹配
        clearNoNum(value, name) {
            value = value.toString();
            value = value.replace(/[^\d.]/g, ''); //清除“数字”和“.”以外的字符
            value = value.replace(/\.{2,}/g, '.'); //只保留第一个. 清除多余的
            value = value
                .replace('.', '$#$')
                .replace(/\./g, '')
                .replace('$#$', '.');
            value = value.replace(/^(\-)*(\d+)\.(\d\d).*$/, '$1$2.$3'); //只能输入两个小数
            if (value.indexOf('.') < 0 && value != '') {
                //以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额
                value = parseFloat(value);
            }
            if (name == 'price') {
                this.addPrice = value;
            } else if (name == 'lastNum') {
                this.preDegree = value;
            } else if (name == 'nowNum') {
                this.Degree = value;
            }
        },
        // 费用项目-添加
        addRight(type) {
            this.add_or_edeit = type;
            let _list = [];
            this.oldLifeCosts.forEach((item) => {
                let lt = this.lifeCosts.map((itm) => itm.dictLabel);
                if (!lt.includes(item.dictLabel)) {
                    _list.push(item);
                }
            });
            this.columns = _list;
            if (this.columns.length > 0) {
                this.addShow = true;
                this.title = this.columns[0].dictLabel;
                this.dictSort = this.columns[0].dictSort;
                this.gasMode = this.columns[0].gasMode;
                this.objItem = '';
                this.addPrice = '';
                this.preDegree = '';
                this.Degree = '';
            } else {
                Toast({
                    message: '暂无可添加费用项目',
                    duration: 900
                });
                return;
            }
        },
        // 选择-费用项目
        onConfirm(value) {
            this.title = value.dictLabel;
            this.dictSort = value.dictSort;
            this.addObjShow = false;
        },
        // 选择-收费方式
        onConfirmCostPayType(e) {
            this.lifeCost_mode = e;
            let mode = this.payOldAll.find((item) => item.dictLabel == e).dictValue;
            if (this.dictSort == 1) {
                this.elecMode = mode;
            } else if (this.dictSort == 2) {
                this.waterMode = mode;
            } else if (this.dictSort == 3) {
                this.gasMode = mode;
            }
            this.show_lifeCostPay = false;
        },
        // 新增或编辑-确定
        addSure() {
            if (!this.addPrice) {
                Toast({
                    message: '请输入单价',
                    duration: 900
                });
                return;
            }
            if (this.dictSort < 4) {
                if (!this.Degree && this.lifeCost_mode == '抄表计算' && this.add_or_edeit == 'add') {
                    Toast({
                        message: '请输入本次读数',
                        duration: 900
                    });
                    return;
                }
                if (+this.Degree < +this.preDegree) {
                    Toast({
                        message: '上次读数应小于等于本次读数',
                        duration: 900
                    });
                    this.Degree = '';
                    return;
                }
            }
            if (!this.objItem) {
                // 新增
                this.addSave();
            } else {
                // 编辑
                this.oldLifeCosts.find((item) => item.dictSort == this.objItem.dictSort).Price = this.addPrice;
                this.oldLifeCosts.find((item) => item.dictSort == this.objItem.dictSort).Degree = this.Degree;
                this.oldLifeCosts.find((item) => item.dictSort == this.objItem.dictSort).preDegree = this.preDegree;
                this.lifeCosts = this.oldLifeCosts.filter((item) => item.Price > 0);
                this.addShow = false;
            }
        },
        // 生活账单-新增、编辑项目
        addSave() {
            let data = {
                checkType: this.dictSort,
                price: Number(this.addPrice).toFixed(2)
            };
            if (this.dictSort < 4) {
                data.preDegree = Number(this.preDegree).toFixed(2);
                data.degree = Number(this.Degree).toFixed(2);
            }
            if (this.dictSort == 1) {
                data.mode = this.elecMode;
                if (this.elecMode == '2') {
                    delete data.degree;
                    delete data.preDegree;
                }
            } else if (this.dictSort == 2) {
                data.mode = this.waterMode;
                if (this.waterMode == '2') {
                    delete data.degree;
                    delete data.preDegree;
                }
            } else if (this.dictSort == 3) {
                data.mode = this.gasMode;
                if (this.gasMode == '2') {
                    delete data.degree;
                    delete data.preDegree;
                }
            }
            if (this.$route.query.cooperLifeCostBillId) {
                data.cooperLifeCostBillId = this.$route.query.cooperLifeCostBillId;
                PostBusinessCooperLifeCostBillCheckTypeAdd(data).done((res) => {
                    this.getCurrentCooperInfo(this.$route.query.cooperLifeCostBillId);
                });
                this.addShow = false;
            } else {
                data.lifeCostBillId = this.$route.query.lifeCostBillId;
                PostBusinessLifeCostBillCheckTypeAdd(this.$route.query.lifeCostBillId, data).done((res) => {
                    this.getCurrentInfo(this.$route.query.lifeCostBillId);
                });
                this.addShow = false;
            }
        },
        // 生活账单移除项目
        remove(item) {
            MessageBox.confirm('', {
                message: `确认删除${item.dictLabel}？`,
                title: '提示',
                cancelButtonText: '取消',
                confirmButtonText: '确定'
            })
                .then((action) => {
                    if (action == 'confirm') {
                        let data = {
                            checkType: item.dictSort
                        };
                        if (!this.$route.query.cooperLifeCostBillId) {
                            PostBusinessLifeCostBillCheckTypeRemove(this.$route.query.lifeCostBillId, data).done((res) => {
                                Toast({
                                    message: res.msg,
                                    duration: 900
                                });
                                this.getCurrentInfo(this.$route.query.lifeCostBillId);
                            });
                        } else {
                            data.cooperLifeCostBillId = this.$route.query.cooperLifeCostBillId;
                            PostBusinessCooperLifeCostBillCheckTypeRemove(data).done((res) => {
                                this.getCurrentCooperInfo(this.$route.query.cooperLifeCostBillId);
                            });
                        }
                    }
                })
                .catch((e) => {
                    console.log(e);
                });
        },
        // 编辑
        setItem(item, type) {
            this.add_or_edeit = type;
            if (item.dictSort < 4) {
                this.addShow = true;
                this.objItem = item;
                this.addPrice = this.objItem.Price;
                this.preDegree = this.objItem.preDegree;
                this.Degree = this.objItem.Degree;
                this.dictSort = this.objItem.dictSort;
                if (this.objItem.dictSort == 1) {
                    this.lifeCost_mode = this.payOldAll.find((item) => item.dictValue == this.objItem.elecMode).dictLabel;
                } else if (this.objItem.dictSort == 2) {
                    this.lifeCost_mode = this.payOldAll.find((item) => item.dictValue == this.objItem.waterMode).dictLabel;
                } else if (this.objItem.dictSort == 3) {
                    this.lifeCost_mode = this.payOldAll.find((item) => item.dictValue == this.objItem.gasMode).dictLabel;
                }
                this.detailPrice = (this.Degree - this.preDegree) * this.addPrice;
            }
        },
        // 提交账单
        saveBack() {
            let data = {};
            data.id = this.$route.query.cooperLifeCostBillId;
            this.lifeCosts.map((item) => {
                if (item.dictSort < 4) {
                    if (item.dictSort == 1 && item.elecMode == '1') {
                        data.elecDegree = this.lifeCosts.find((item) => item.dictSort == 1).Degree;
                        data.elecMode = item.elecMode;
                    } else if (item.dictSort == 2 && item.waterMode == '1') {
                        data.waterDegree = this.lifeCosts.find((item) => item.dictSort == 2).Degree;
                        data.waterMode = item.waterMode;
                    } else if (item.dictSort == 3 && item.gasMode == '1') {
                        data.gasDegree = this.lifeCosts.find((item) => item.dictSort == 3).Degree;
                        data.gasMode = item.gasMode;
                    }
                }
                data[item.dictValue] = item.Price;
            });
            MessageBox.confirm('', {
                message: '确认提交账单？',
                title: '提示',
                cancelButtonText: '取消',
                confirmButtonText: '确定'
            })
                .then((action) => {
                    if (action == 'confirm') {
                        if (!this.$route.query.cooperLifeCostBillId) {
                            data.id = this.$route.query.lifeCostBillId;
                            PostBusinessLifeCostBillEdit(data).done((res) => {
                                Toast({
                                    message: res.msg,
                                    duration: 900
                                });
                                setTimeout('window.history.go(-1)', 900);
                                localStorage.removeItem('life_fee');
                            });
                        } else {
                            PostBusinessCooperLifeCostBillEdit(data).done((res) => {
                                Toast({
                                    message: res.msg,
                                    duration: 900
                                });
                                setTimeout('window.history.go(-1)', 900);
                                localStorage.removeItem('life_fee');
                            });
                        }
                    }
                })
                .catch((e) => {
                    console.log(e);
                });
        }
    }
};
</script>
